import React from 'react';
import classes from './Post.module.css'

const Post = (props) => {
  return (
    <div className={classes.item}>
      
      <img alt="" src="data:image/jpeg;base64,"></img>
      <div>{props.message}</div>
      <div>
        <span >Like</span> 
        {props.likesCount}
      </div>
    </div>
  )
}
export default Post;